import { API_URL, fetchApi } from '../../api';
import { stringifySerializedElement } from '../../functions/serialize';
import { isScriptableUrl } from '../../functions/url';
import { sendChromeMessage } from '../../functions/contentScriptUtils';
import * as Sentry from '@sentry/browser';

/* ================== Global Variables ================== */
let isRecording = false;
let startingTabs = [];
let activeRecordingSteps = [];

/* ================== Utilities ================== */
const getAllTabsInWindow = async () => {
  const allTabs = await chrome.tabs.query({
    currentWindow: true,
  });

  const validTabs = allTabs.filter((tab) => isScriptableUrl(tab.url));

  return validTabs;
};

const getAllFramesInTab = async (tabId) => {
  const allFrames = await chrome.webNavigation.getAllFrames({
    tabId,
  });

  const validFrames = allFrames.filter((frame) => isScriptableUrl(frame.url));

  return validFrames;
};

const getFrameFromSrc = async (tabId, frameSrc) => {
  const allFrames = await getAllFramesInTab(tabId);

  for (const frame of allFrames) {
    if (frame.url === frameSrc) {
      return frame;
    }
  }
};

const getStartingTabs = async () => {
  const allTabs = await chrome.tabs.query({
    currentWindow: true,
  });

  return allTabs.map((tab) => ({
    url: tab.url,
    isActive: tab.active,
  }));
};

const getActiveTabIndex = async () => {
  const allTabs = await chrome.tabs.query({
    currentWindow: true,
  });

  const activeTabIndex = allTabs.findIndex((tab) => tab.active);

  return activeTabIndex;
};

const getConstructedElementCapture = async (eventCaptured) => {
  const { timestamp } = eventCaptured;
  const { serializedElement, screenshotCoordinates } = eventCaptured.details;
  const screenshot = await chrome.tabs.captureVisibleTab({
    format: 'png',
  });
  const uploadedScreenshot = await fetchApi(
    API_URL.UPLOAD_SCREENSHOT_TO_STORAGE,
    {
      screenshot,
      screenshot_coordinates: screenshotCoordinates,
    }
  );

  const step = {
    type: eventCaptured.type,
    details: {
      element: stringifySerializedElement(serializedElement),
      screenshot: uploadedScreenshot.screenshot_url,
    },
    timestamp,
  };

  return step;
};

const constructStepsWithCombinedText = (steps) => {
  const combinedEvents = [];
  let combinedText = '';

  for (let i = 0; i < steps.length; i++) {
    const event = steps[i];

    if (event.type === 'text_typed' || event.type === 'backspace_pressed') {
      if (event.type === 'backspace_pressed' && combinedText === '') {
        combinedEvents.push(event);
        continue;
      } else if (event.type === 'backspace_pressed') {
        combinedText = combinedText.slice(0, -1);
      } else if (event.type === 'text_typed') {
        combinedText += event.details.text;
      }
    } else {
      if (combinedText) {
        combinedEvents.push({
          type: 'text_typed',
          details: { text: combinedText },
          timestamp: steps[i - 1].timestamp,
        });
        combinedText = '';
      }
      combinedEvents.push(event);
    }
  }

  if (combinedText) {
    combinedEvents.push({
      type: 'text_typed',
      details: { text: combinedText },
      timestamp: steps[steps.length - 1].timestamp,
    });
  }

  return combinedEvents;
};

const constructStepsWithCombinedBackSpaces = (steps) => {
  const combinedEvents = [];
  let backspaceCount = 0;

  for (let i = 0; i < steps.length; i++) {
    const event = steps[i];

    if (event.type === 'backspace_pressed') {
      backspaceCount++;
    } else {
      if (backspaceCount) {
        combinedEvents.push({
          type: 'backspace_pressed',
          details: { count: backspaceCount },
          timestamp: steps[i - 1].timestamp,
        });
        backspaceCount = 0;
      }

      combinedEvents.push(event);
    }
  }

  if (backspaceCount) {
    combinedEvents.push({
      type: 'backspace_pressed',
      details: { count: backspaceCount },
      timestamp:
        activeRecordingSteps[activeRecordingSteps.length - 1].timestamp,
    });
  }

  return combinedEvents;
};

const cleanStartingTabsToUsedTabsOnlyAndUpdateSteps = (startingTabs, steps) => {
  const referencedIndices = new Set();
  const referencedURLs = new Set();

  steps.forEach((step) => {
    if (step.activeTabIndex !== undefined) {
      referencedIndices.add(step.activeTabIndex);
      referencedURLs.add(startingTabs[step.activeTabIndex]?.url);
    }

    if (step.details?.url) {
      referencedURLs.add(step.details.url);
    }
  });

  const updatedStartingTabs = [];
  const urlToNewIndexMap = new Map();

  startingTabs.forEach((tab, index) => {
    if (referencedURLs.has(tab.url) && !urlToNewIndexMap.has(tab.url)) {
      urlToNewIndexMap.set(tab.url, updatedStartingTabs.length);
      updatedStartingTabs.push(tab);
    } else if (referencedIndices.has(index)) {
      urlToNewIndexMap.set(tab.url, updatedStartingTabs.length);
      updatedStartingTabs.push(tab);
    }
  });

  const updatedSteps = steps.map((step) => ({
    ...step,
    activeTabIndex:
      step.activeTabIndex !== undefined
        ? urlToNewIndexMap.get(startingTabs[step.activeTabIndex]?.url)
        : undefined,
    details:
      step.details?.tabIndex !== undefined
        ? {
            ...step.details,
            tabIndex: urlToNewIndexMap.get(
              startingTabs[step.details.tabIndex]?.url
            ),
          }
        : step.details,
  }));

  return { startingTabs: updatedStartingTabs, steps: updatedSteps };
};

const createWorkflow = async (orgId, userId, type) => {
  const stepsWithCombinedText =
    constructStepsWithCombinedText(activeRecordingSteps);
  const finalRecordingSteps = constructStepsWithCombinedBackSpaces(
    stepsWithCombinedText
  );

  const { startingTabs: updatedStartingTabs, steps: updatedSteps } =
    cleanStartingTabsToUsedTabsOnlyAndUpdateSteps(
      startingTabs,
      finalRecordingSteps
    );

  const stepsWithoutActiveTabIndex = updatedSteps.map((step) => {
    const { activeTabIndex, ...rest } = step;
    return rest;
  });

  let response;

  if (type === 'personal') {
    response = await fetchApi(API_URL.CREATE_PERSONAL_WORKFLOW, {
      user_id: userId,
      steps: stepsWithoutActiveTabIndex,
      starting_tabs: updatedStartingTabs,
    });
  } else if (type === 'organization') {
    response = await fetchApi(API_URL.CREATE_ORGANIZATION_WORKFLOW, {
      user_id: userId,
      org_id: orgId,
      steps: stepsWithoutActiveTabIndex,
      starting_tabs: updatedStartingTabs,
    });
  } else {
    throw new Error('INVALID_TYPE');
  }

  return response.workflow_id;
};

/* ================== Uninstall Feedback URL ================== */
chrome.runtime.onInstalled.addListener(async (details) => {
  if (details.reason === chrome.runtime.OnInstalledReason.INSTALL) {
    // chrome.runtime.setUninstallURL('https://forms.gle/VRkFzJHjv5LEWFWc8');
  }

  chrome.sidePanel.setOptions(
    {
      enabled: true,
      path: './router.html',
    },
    () => {}
  );

  const allTabs = await chrome.tabs.query({});

  for (const tab of allTabs) {
    if (tab.id && isScriptableUrl(tab.url)) {
      await chrome.scripting.executeScript({
        target: { tabId: tab.id, allFrames: true },
        files: ['contentScripts.js'],
      });
    }
  }
});

/* ================== Side Panel Initialization ================== */
chrome.action.onClicked.addListener((tab) => {
  chrome.sidePanel.open({ windowId: tab.windowId });
});

/* ================== URL & Tab Change Listeners ================== */
chrome.webNavigation.onCommitted.addListener(async (details) => {
  if (
    details.transitionQualifiers &&
    details.transitionQualifiers.includes('from_address_bar')
  ) {
    if (!isRecording) {
      return;
    }

    activeRecordingSteps.push({
      type: 'url_change',
      details: {
        url: details.url,
        isInNewTab: false,
      },
      timestamp: Date.now(),
    });
  }
});

chrome.tabs.onUpdated.addListener(async (tabId, changeInfo, tab) => {
  if (changeInfo.status === 'complete') {
    if (isScriptableUrl(tab.url)) {
      await chrome.scripting.executeScript({
        target: { tabId, allFrames: true },
        files: ['contentScripts.js'],
      });
    }

    if (!isRecording) {
      return;
    }

    if (isScriptableUrl(tab.url)) {
      await sendChromeMessage(tabId, {
        action: 'start_recording',
      });

      if (!tab.active) {
        activeRecordingSteps.push({
          type: 'url_change',
          details: {
            url: tab.url,
            isInNewTab: true,
          },
          timestamp: Date.now(),
        });
      }
    }
  }
});

chrome.tabs.onActivated.addListener(async (activeInfo) => {
  const { tabId } = activeInfo;

  if (!isRecording) {
    return;
  }

  const timestamp = Date.now();

  const allWindowTabs = await chrome.tabs.query({
    currentWindow: true,
  });
  const currentTabIndex = allWindowTabs.findIndex((tab) => tab.id === tabId);
  const currentTab = allWindowTabs[currentTabIndex];

  if (
    currentTab.url === 'chrome://newtab/' ||
    currentTab.pendingUrl === 'chrome://newtab/'
  ) {
    activeRecordingSteps.push({
      type: 'new_tab',
      details: {},
      timestamp,
    });
  } else {
    activeRecordingSteps.push({
      type: 'switch_tab',
      details: {
        tabIndex: currentTabIndex,
      },
      timestamp,
    });
  }
});

/* ================== Recording Message Handling ================== */
let recordPort;
let workflowPort;
let updateStepPort;

chrome.runtime.onConnect.addListener((port) => {
  if (port.name === 'workflow') {
    workflowPort = port;
  } else if (port.name === 'record') {
    recordPort = port;
  } else if (port.name === 'update_step') {
    updateStepPort = port;
  }

  port.onMessage.addListener(async (message, sender) => {
    if ('action' in message) {
      if (message.action === 'start_recording') {
        const allWindowTabs = await getAllTabsInWindow();

        let successfullyStartedRecording = true;

        for (const tab of allWindowTabs) {
          const resultOfMessage = await sendChromeMessage(tab.id, {
            action: 'start_recording',
          });

          if (!resultOfMessage) {
            successfullyStartedRecording = false;
            break;
          }
        }

        if (!successfullyStartedRecording) {
          if (recordPort) {
            recordPort.postMessage({
              action: 'error_starting_recording',
            });
          }
        } else {
          isRecording = true;
          startingTabs = await getStartingTabs();
        }
      } else if (message.action === 'new_iframe_added') {
        const { id: tabId } = sender.sender.tab;
        const { frameSrc } = message;

        const frame = await getFrameFromSrc(tabId, frameSrc);

        if (frame && !frame.errorOccurred) {
          await chrome.scripting.executeScript({
            target: { tabId, frameIds: [frame.frameId] },
            files: ['contentScripts.js'],
          });

          if (isRecording) {
            await sendChromeMessage(
              tabId,
              {
                action: 'start_recording',
              },
              { frameId: frame.frameId }
            );
          }
        }
      } else if (message.action === 'stop_recording') {
        const { orgId, userId, type } = message;

        const allWindowTabs = await getAllTabsInWindow();

        let successfullyStoppedRecording = true;

        for (const tab of allWindowTabs) {
          const resultOfMessage = await sendChromeMessage(tab.id, {
            action: 'stop_recording',
          });

          if (!resultOfMessage) {
            successfullyStoppedRecording = false;
            break;
          }
        }

        if (!successfullyStoppedRecording) {
          isRecording = false;
          startingTabs = [];
          activeRecordingSteps = [];

          if (recordPort) {
            recordPort.postMessage({
              action: 'error_stopping_recording',
            });
          }
        } else {
          isRecording = false;

          while (
            activeRecordingSteps.map((step) => step.type).includes('loading')
          ) {
            await new Promise((resolve) => setTimeout(resolve, 500));
          }

          if (activeRecordingSteps.length > 0) {
            const workflowId = await createWorkflow(orgId, userId, type);

            startingTabs = [];
            activeRecordingSteps = [];

            if (recordPort) {
              recordPort.postMessage({
                action: 'workflow_created',
                workflowId,
              });
            }
          } else {
            startingTabs = [];
            activeRecordingSteps = [];

            if (recordPort) {
              recordPort.postMessage({
                action: 'empty_workflow',
              });
            }
          }
        }
      } else if (message.action === 'event_captured') {
        const { eventCaptured } = message;

        const activeTabIndex = await getActiveTabIndex();

        if (
          eventCaptured.type === 'mouse_click' ||
          eventCaptured.type === 'copy' ||
          eventCaptured.type === 'paste'
        ) {
          let indexOfNewEvent = activeRecordingSteps.length;
          activeRecordingSteps[indexOfNewEvent] = {
            type: 'loading',
            timestamp: eventCaptured.timestamp,
          };

          const constructedEvent =
            await getConstructedElementCapture(eventCaptured);

          activeRecordingSteps[indexOfNewEvent] = {
            ...constructedEvent,
            activeTabIndex,
          };
        } else {
          activeRecordingSteps.push({
            ...eventCaptured,
            activeTabIndex,
          });
        }
      } else if (message.action === 'listen_for_mouse_click') {
        const allTabs = await getAllTabsInWindow();

        for (const tab of allTabs) {
          await sendChromeMessage(
            tab.id,
            {
              action: 'listen_for_mouse_click',
            },
            { frameId: 0 }
          );
        }
      } else if (message.action === 'listen_for_copy') {
        const allTabs = await getAllTabsInWindow();

        for (const tab of allTabs) {
          await sendChromeMessage(
            tab.id,
            {
              action: 'listen_for_copy',
            },
            { frameId: 0 }
          );
        }
      } else if (message.action === 'listen_for_paste') {
        const allTabs = await getAllTabsInWindow();

        for (const tab of allTabs) {
          await sendChromeMessage(
            tab.id,
            {
              action: 'listen_for_paste',
            },
            { frameId: 0 }
          );
        }
      } else if (message.action === 'listener_mouse_click_captured') {
        const { eventCaptured } = message;

        if (workflowPort) {
          workflowPort.postMessage({
            action: 'handle_listener_mouse_click',
            eventCaptured,
          });
        }

        if (updateStepPort) {
          updateStepPort.postMessage({
            action: 'handle_listener_mouse_click',
            eventCaptured,
          });
        }
      } else if (message.action === 'listener_copy_captured') {
        const { eventCaptured } = message;

        if (workflowPort) {
          workflowPort.postMessage({
            action: 'handle_listener_copy',
            eventCaptured,
          });
        }

        if (updateStepPort) {
          updateStepPort.postMessage({
            action: 'handle_listener_copy',
            eventCaptured,
          });
        }
      } else if (message.action === 'listener_paste_captured') {
        const { eventCaptured } = message;

        if (workflowPort) {
          workflowPort.postMessage({
            action: 'handle_listener_paste',
            eventCaptured,
          });
        }

        if (updateStepPort) {
          updateStepPort.postMessage({
            action: 'handle_listener_paste',
            eventCaptured,
          });
        }
      } else if (message.action === 'init_sentry') {
        const { environment } = message;

        if (environment === 'production') {
          Sentry.init({
            dsn: 'https://f1970ea44d9f0abc6831745e34c2436f@o4504812880789504.ingest.us.sentry.io/4508509986881536',
          });
        }
      } else if (message.action === 'error_in_content_script') {
        const { errorMessage } = message;

        Sentry.captureException(new Error(errorMessage));
      }
    }
  });
});
